"use client";

import React, { useState, useCallback, Suspense, useEffect } from "react";
import { motion } from "framer-motion";
import { Skeleton } from "@/components/ui/skeleton";
import Image from "next/image";
import categoriesData from "@/data/categories.json";
import "swiper/css";
import "swiper/css/navigation";
export interface Product {
  id: string;
  name: string;
  description: string;
  image: string;
  details?: string;
}

export interface Category {
  id: string;
  label: string;
  products: Product[];
}

const useProductData = () => {
  const [categories, setCategories] = useState<Category[]>([]);
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    const loadData = async () => {
      await new Promise(resolve => setTimeout(resolve, 1000));
      setCategories(categoriesData);
      setLoading(false);
    };
    loadData();
  }, []);

  return { categories, loading };
};

const SkeletonLoader = () => (
  <div className="grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-3 gap-6 md:gap-8">
    {[1, 2, 3].map((item) => (
      <div key={item} className="relative rounded-2xl overflow-hidden bg-white border border-gray-200 shadow-lg">
        <Skeleton className="w-full aspect-video rounded-none" />
        <div className="p-4 sm:p-5 space-y-3">
          <Skeleton className="h-6 w-3/4" />
          <div className="space-y-2">
            <Skeleton className="h-4 w-full" />
            <Skeleton className="h-4 w-2/3" />
          </div>
          <Skeleton className="h-10 w-full rounded-lg" />
        </div>
      </div>
    ))}
  </div>
);

const COLORS = {
  background: "bg-white",
  text: {
    primary: "text-gray-900",
    secondary: "text-gray-600",
    muted: "text-gray-500",
  },
  accent: {
    primary: "bg-emerald-500 hover:bg-emerald-600",
    secondary: "bg-gray-100 hover:bg-gray-200",
  },
  border: "border-gray-200",
};

const ProductCard: React.FC<{ product: Product; onSelect: (product: Product) => void }> = ({ product, onSelect }) => {
  return (
    <motion.div
      className={`relative rounded-2xl overflow-hidden ${COLORS.background} border ${COLORS.border} 
      shadow-lg transition-all duration-300 hover:shadow-xl group focus:outline-none focus:ring-2 focus:ring-emerald-300`}
      whileHover={{
        scale: 1.025,
        boxShadow: "0 15px 30px -10px rgba(0, 0, 0, 0.1)",
      }}
      whileTap={{ scale: 0.98 }}
    >
      <div className="relative w-full aspect-video overflow-hidden">
        <Image
          src={product.image}
          alt={product.name}
          fill
          sizes="(max-width: 768px) 100vw, (max-width: 1200px) 50vw, 33vw"
          className="object-cover transition-transform duration-300 group-hover:scale-110"
        />
      </div>

      <div className="p-4 sm:p-5 space-y-3">
        <h3
          className={`text-lg sm:text-xl font-bold ${COLORS.text.primary} group-hover:text-emerald-600 transition-colors`}
        >
          {product.name}
        </h3>

        <p className={`${COLORS.text.secondary} text-sm line-clamp-3`}>{product.description}</p>

        <button
          onClick={() => onSelect(product)}
          className={`w-full py-2 rounded-lg ${COLORS.accent.primary} text-white font-semibold 
          transition-all duration-300 hover:scale-[1.02] active:scale-[0.98] focus:outline-none focus:ring-2 focus:ring-emerald-300`}
        >
          Ver Detalhes
        </button>
      </div>
    </motion.div>
  );
};

const ProductModal: React.FC<{ product: Product | null; onClose: () => void }> = ({ product, onClose }) => {
  if (!product) return null;

  return (
    <motion.div
      initial={{ opacity: 0 }}
      animate={{ opacity: 1 }}
      exit={{ opacity: 0 }}
      className="fixed inset-0 z-50 flex items-center justify-center p-4 bg-black/50 backdrop-blur-sm"
    >
      <motion.div
        initial={{ scale: 0.9, opacity: 0 }}
        animate={{ scale: 1, opacity: 1 }}
        transition={{ type: "spring", stiffness: 300, damping: 20 }}
        className="w-full max-w-4xl bg-white rounded-2xl overflow-hidden flex flex-col md:grid md:grid-cols-2 shadow-2xl max-h-[90vh]"
      >
        {/* Image Section */}
        <div className="relative aspect-square md:aspect-auto md:h-full">
          <Image
            src={product.image}
            alt={product.name}
            fill
            className="object-cover"
            sizes="(max-width: 768px) 100vw, 50vw"
          />
        </div>

        {/* Details Section */}
        <div className="p-6 md:p-8 flex flex-col overflow-hidden">
          {/* Scrollable Content */}
          <div className="flex-1 overflow-y-auto pr-2 -mr-2 scrollbar-thin scrollbar-thumb-[#177f0f]/20 scrollbar-track-transparent">
            <h2 className={`text-2xl md:text-3xl font-bold ${COLORS.text.primary} mb-4`}>
              {product.name}
            </h2>

            <div className="space-y-6">
              <p className={`${COLORS.text.secondary} leading-relaxed`}>
                {product.description}
              </p>

              {product.details && (
                <div>
                  <h3 className={`text-lg font-semibold ${COLORS.text.primary} mb-3`}>
                    Detalhes Técnicos
                  </h3>
                  <p className={`${COLORS.text.muted} leading-relaxed`}>
                    {product.details}
                  </p>
                </div>
              )}
            </div>
          </div>

          {/* Fixed Buttons */}
          <div className="flex space-x-3 pt-6 mt-6 border-t border-gray-200">
            <button
              onClick={onClose}
              className={`flex-1 py-2.5 rounded-lg ${COLORS.accent.secondary} ${COLORS.text.primary} font-semibold transition-all hover:opacity-90`}
            >
              Fechar
            </button>
            <button
              onClick={() => window.open(`https://wa.me/558532262933?text=Olá, gostaria de saber mais sobre o produto ${product.name}`, '_blank')}
              className={`flex-1 py-2.5 rounded-lg ${COLORS.accent.primary} text-white font-semibold transition-all hover:opacity-90`}
            >
              Comprar
            </button>
          </div>
        </div>
      </motion.div>
    </motion.div>
  );
};

export default function ProductsPage() {
  return (
    <section className="min-h-screen bg-white py-12 md:py-16 lg:py-20">
      <div className="container mx-auto px-6 sm:px-8 lg:px-12">
        <Suspense fallback={<SkeletonLoader />}>
          <ProductsSection />
        </Suspense>
      </div>
    </section>
  );
}

function ProductsSection() {
  const { categories, loading } = useProductData();
  const [selectedProduct, setSelectedProduct] = useState<Product | null>(null);
  const [activeCategory, setActiveCategory] = useState(0);

  const handleProductSelect = useCallback((product: Product) => {
    setSelectedProduct(product);
  }, []);

  if (loading) return <SkeletonLoader />;

  return (
    <>
      {/* Section Header */}
      <div className="text-center mb-10 md:mb-12">
        <h2 className="text-3xl md:text-4xl font-bold text-gray-900 mb-4">
          Nossos Produtos
        </h2>
        <p className="text-base md:text-xl max-w-2xl mx-auto text-gray-600">
          Descubra nossa linha completa de produtos de alta qualidade
        </p>
      </div>

      {/* Category Navigation */}
      <div className="flex justify-center mb-8 md:mb-12 space-x-2 md:space-x-4">
        {categories.map((category, index) => (
          <button
            key={category.id}
            onClick={() => setActiveCategory(index)}
            className={`px-4 md:px-6 py-2 rounded-lg text-sm md:text-base font-medium transition-all duration-300 ${
              activeCategory === index
                ? "bg-emerald-500 text-white"
                : "bg-gray-100 text-gray-700 hover:bg-gray-200"
            }`}
          >
            {category.label}
          </button>
        ))}
      </div>

      {/* Products Grid */}
      <div className="grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-3 gap-6 md:gap-8">
        {categories[activeCategory]?.products.map((product) => (
          <ProductCard
            key={product.id}
            product={product}
            onSelect={handleProductSelect}
          />
        ))}
      </div>

      {/* Product Modal */}
      {selectedProduct && (
        <ProductModal
          product={selectedProduct}
          onClose={() => setSelectedProduct(null)}
        />
      )}
    </>
  );
}